import { useEffect, useState } from 'react'
import { Helmet } from 'react-helmet-async'
import { useNavigate, useParams } from 'react-router'

import { Box, Card, Container, Grid, TextField, Typography, useTheme } from '@mui/material'
import {
  DataGrid,
  type GridRowSelectionModel,
  GridToolbarColumnsButton,
  GridToolbarContainer,
  GridToolbarDensitySelector,
  GridToolbarExport,
  GridToolbarFilterButton,
  GridToolbarQuickFilter
} from '@mui/x-data-grid'
import { LocalizationProvider, StaticDatePicker } from '@mui/x-date-pickers'
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs'

import { MEETING_MANAGE_COLUMN } from 'src/data/meetingManage'

import CancelSubmitBtns from 'src/components/CancelSubmitBtns'

import {
  useGetConferenceRound,
  useGetConferenceRoundAttendance,
  usePatchConferenceRound,
  usePutConferenceRoundAttendance
} from 'src/hooks/api/clubConferenceHooks'
import { type attendanceStatus } from 'src/models/conference'

const MeetingManage = () => {
  const theme = useTheme()
  const navigate = useNavigate()
  const [selectedMembers, setSelectedMembers] = useState<number[]>([])
  const [selectedDate, setSelectedDate] = useState<Date | null>(null)

  const { id, confId, meetingId } = useParams()
  const clubId = Number(id)
  const conferenceId = Number(confId)
  const conferenceRoundId = Number(meetingId)
  const { data: round, isSuccess: isRoundSuccess } = useGetConferenceRound(clubId, conferenceId, conferenceRoundId)
  const { data: attendance, isSuccess: isAttendanceSuccess } = useGetConferenceRoundAttendance(
    clubId,
    conferenceId,
    conferenceRoundId
  )
  const patchRoundMutation = usePatchConferenceRound()
  const putRoundAttendanceMutation = usePutConferenceRoundAttendance(clubId, conferenceId, conferenceRoundId)

  const handleSubmit = () => {
    if (!selectedDate) return
    if (isRoundSuccess && isAttendanceSuccess) {
      const selected = new Set(selectedMembers)
      const status = attendance.conferenceParticipants
        .filter(participant => participant.attendances.length > 0)
        .map(participant => ({
          id: participant.attendances[0].id,
          attendanceType: selected.has(participant.user.id) ? 1 : 0 as attendanceStatus
        }))
      patchRoundMutation.mutate({ clubId, conferenceId, conferenceRoundId, note: round?.note, startAt: selectedDate })
      putRoundAttendanceMutation.mutate(status)
    }
    navigate(-1)
  }

  useEffect(() => {
    if (round?.startAt) setSelectedDate(round?.startAt)
  }, [round])

  useEffect(() => {
    const selected: number[] = []
    attendance?.conferenceParticipants?.forEach(participant => {
      if (
        participant.attendances.find(attendance => attendance.conferenceRoundId == conferenceRoundId)?.attendanceType ==
          1 &&
        participant.user.id
      )
        selected.push(participant.user.id)
    })
    setSelectedMembers(selected)
  }, [attendance])

  const handleRowSelectionChange = (selectedMemberIds: GridRowSelectionModel) => {
    setSelectedMembers(selectedMemberIds as number[])
  }

  const handleSelectedDateChange = (date: Date | null) => {
    if (date == null) return
    setSelectedDate(date)
  }

  const handleReset = () => {
    if (round?.startAt) setSelectedDate(round?.startAt)
  }

  const Toolbar = () => {
    return (
      <GridToolbarContainer sx={{ justifyContent: 'space-between' }}>
        <GridToolbarColumnsButton />
        <GridToolbarFilterButton sx={{ '& svg': { width: '18px', height: '18px' } }} />
        <GridToolbarDensitySelector />
        <GridToolbarExport />
        <GridToolbarQuickFilter />
      </GridToolbarContainer>
    )
  }

  return (
    <Box m={3}>
      <Helmet>
        <title>{`${round ? round?.round : ''}회차 관리`}</title>
      </Helmet>
      {/* <PageTitleWrapper>
        <PageHeader clubName={''} buttonText="행사 추가" link={`/manage/${clubId}/attendance/register`} />
      </PageTitleWrapper> */}
      <Container>
        <Card>
          <Box m={3}>
            <Grid container sx={{ justifyContent: 'center' }}>
              <Grid item xs={12} md={7.5}>
                <Typography variant="h4" component="h4">
                  {`${round ? round?.round : ''}회차 관리`}
                </Typography>
                <Box sx={{ mt: 1 }}>
                  <Box display="flex" alignItems="baseline">
                    <Typography variant="h4" color={theme.colors.primary.main} display="inline-block" whiteSpace='nowrap'>
                      출석
                    </Typography>
                    <Typography variant="caption" marginLeft="0.4em">
                      행사에 참여한 회원을 모두 체크해주세요
                    </Typography>
                  </Box>
                  <DataGrid
                    autoHeight
                    checkboxSelection
                    rowSelectionModel={selectedMembers}
                    onRowSelectionModelChange={handleRowSelectionChange}
                    rows={attendance?.conferenceParticipants.map(participant => participant.user) ?? []}
                    columns={MEETING_MANAGE_COLUMN}
                    slots={{ toolbar: Toolbar }}
                    disableRowSelectionOnClick
                    sx={{ mt: 1 }}
                  />
                </Box>
              </Grid>
              <Grid item xs={12} md={4.5}>
                {selectedDate && (
                  <Box paddingLeft={{ xs: 3 }} paddingBottom={{ xs: 0 }}>
                    <LocalizationProvider dateAdapter={AdapterDayjs}>
                      <StaticDatePicker
                        // orientation='landscape'
                        label={`${round ? round?.round : ''}회차 일자`}
                        value={selectedDate}
                        onChange={handleSelectedDateChange}
                        componentsProps={{ actionBar: { actions: [] } }} // 확인 취소 버튼 없애기
                        renderInput={params => <TextField sx={{ display: 'none' }} {...params} />}
                      />
                    </LocalizationProvider>
                  </Box>
                )}
              </Grid>
            </Grid>
            <CancelSubmitBtns handleSubmit={handleSubmit} submitBtnText="저장" />
          </Box>
        </Card>
      </Container>
    </Box>
  )
}

export default MeetingManage
